import React from 'react';

const StepsComponent = ({steps = []}) =>
    <div id="StepsComponent">

        <h1 className="text-align-center font-weight-light color-primary">How it Works</h1>

        <div>

            {steps.map((step, id) => {
                const ICON = step.icon;
                return <div key={id} className="stepContainer">

                    <ICON className="stepIcon"/>
                    
                    <h2>{step.title}</h2>

                    <p className="stepDescription">{step.description}</p>

                </div>
            })}

        </div>

    </div>;

export default StepsComponent;